import { Card } from "@/lib/entity/card";

type html = string; //not very useful, but makes the types read a bit better

/*
 * A template controls how to display the frontside and backside of a flashcard.
 *
 * The template controls what fields to display on the card's note, and the styling.
 * It actually has a bit of logic (so there can be error checking if needed) and outputs rendered html.
 *
 * The rendered html is rendered on the front end as unescaped html, so if I want to be secure I need to escape
 * some of the output in the render functions (e.g. escape the data in the card/note)
 * This is not a priority right now though.
 */
interface Template {
  readonly id: string;
  renderLessonFront(card: Card): html;
  renderLessonBack(card: Card): html;
  renderFront(card: Card): html;
  renderBack(card: Card): html;
}

class BasicCardTemplate implements Template {
  constructor(
    readonly id: string,
    readonly frontKey: string,
    readonly backKey: string
  ) {}

  renderLessonFront(card: Card): html {
    const side = card.note.fields[this.frontKey];
    return `<span>${side}</span>`;
  }
  renderLessonBack(card: Card): html {
    const side = card.note.fields[this.backKey];
    return `<span>${side}</span>`;
  }
  renderFront(card: Card): html {
    const side = card.note.fields[this.frontKey];
    return `<span style="font-size: 3em;">${side}</span>`;
  }
  renderBack(card: Card): html {
    const front = card.note.fields[this.frontKey];
    const back = card.note.fields[this.backKey];
    return `
    <span>
      <span style="font-size: 3em;">${back}</span>
      <hr/>
      <span style="font-size: 1.5em;">${front}</span>  
    </span>
    `;
  }
}

export { Template, BasicCardTemplate };
