interface Fields {
  [key: string]: string;
}

/*
  The Note class represents a concept. Data about the concept is stored in the fields.
  
  An example note representing "to go/行く" could be:
  {
    id: "iku_jp",
    fields: {
      eng: "to go",
      jp_romaji: "iku",
      jp_kana: "いく",
      jp_kanji: "行く"
    },
    tags: ["verb", "genki1", "genki_chapter2"]
  }
  
  A card is created from a note (data) and a template (rendering logic)
  From the note and the applicable templates, it would be simple to create two cards using the same note, one going from eng->jp, and one going from jp->eng. 
*/
class Note {
  constructor(
    readonly id: string, // unique identifier for the note
    readonly tags: Array<string>, // array of tags
    readonly fields: Fields // key/value map of fields for the note
  ) {}
}

export { Note };
