











import Vue from "vue";
import LessonRow from "@/vue/components/lesson/LessonRow.vue";

import { cards } from "../util/TestData";
import { Lesson } from "@/lib/value-objects/lesson";

export default Vue.extend({
  beforeCreate: function() {
    const lesson = new Lesson(cards.slice(0, 2));
    if (!(this.$store.state.currentLesson instanceof Lesson)) {
      this.$store.state.currentLesson = lesson; //hardcode the lesson into the state for now
    }
  },
  beforeMount: function() {
    this.$data.lesson.restart();
  },
  data: function() {
    return {
      lesson: this.$store.state.currentLesson
    };
  },
  components: {
    LessonRow: LessonRow
  }
});
