/* eslint-disable @typescript-eslint/camelcase */

import { Note } from "@/lib/entity/note";
import { BasicCardTemplate } from "@/lib/entity/template";
import { Card } from "@/lib/entity/card";
import { genkiNotes } from "./GenkiCards"; //change this to GenkiCardsFull if you only need a subset

const template = new BasicCardTemplate("template_id", "kanji", "english");
const notes = genkiNotes.map(n => {
  return new Note(n.id, n.tags, n.fields);
});
const cards = notes.map(n => {
  return new Card(n, template);
});

export { cards, notes, template };
